import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewEquipo from '@/components/NewEquipo'
import EditEquipo from '@/components/EditEquipo'
import ViewEquipo from '@/components/ViewEquipo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-equipo',
      component: NewEquipo
    },
    {
      path: '/edit/:equipo_id',
      name: 'edit-equipo',
      component: EditEquipo
    },
    {
      path: '/:equipo_id',
      name: 'view-equipo',
      component: ViewEquipo
    },
    {
      path: '/upload',
      name: 'view-equipo',
      component: ViewEquipo
    }
  ]
})



