<template>
	<div id="view-equipo">
		<ul class="collection with-header">
			<li class="collection-header">
				<h4>Ver Equipo {{equipo}} </h4>
				<div class="chip">{{equipo_id}}</div>
			</li>
		</ul>
		<ul class="collection">
    		<li class="collection-item avatar">
    			<img src="/images/yuna.jpg" alt="" class="circle">
    		</li>
    	</ul>
		<router-link to="/" class="btn grey">Atras</router-link>
		<button v-on:click="deleteEquipo" class="btn red">Delete</button>

		<div class="fixed-action-btn">
			<router-link :to="{name: 'edit-equipo', params: {equipo_id: equipo_id}}" class="btn-floating btn-large red">
				<i class="fa fa-pencil"></i>
			</router-link>
		</div>
	</div>
</template>
<script>
import db from './firebaseInit'
export default {
	name: 'view-equipo',
	data () {
		return {
			equipo_id: null,
			equipo: null,
			activo: null
		}
	},
	beforeRouteEnter (to, from, next) {
		db.collection('equipos').where('equipo_id', '==', to.params.equipo_id).get().then(querySnapshot => {
			querySnapshot.forEach(doc => {
				next(vm => {
					vm.equipo_id = doc.data().equipo_id
					vm.equipo = doc.data().equipo
					vm.activo = doc.data().activo
				})
			})
		})
	},
	watch: {
		'$route': 'fetchData'
	},
	methods: {
		fetchData () {
			db.collection('equipos').where('equipo_id', '==', this.$route.params.equipo_id).get().then(querySnapshot => {
			querySnapshot.forEach(doc => {
					this.equipo_id = doc.data().equipo_id
					this.equipo = doc.data().equipo
					this.activo = doc.data().activo
				})
			})
		},
		deleteEquipo () {
			if(confirm('Esta seguro que desea eliminar el Equipo?')) {
				db.collection('equipos').where('equipo_id', '==', this.$route.params.equipo_id).get().then(querySnapshot => {
				querySnapshot.forEach(doc => {
						doc.ref.delete()
						this.$router.push('/')
					})
				})
			}
		}
	}
}
</script>


