<template>
	<div id="new-equipo">
		<h3>Nuevo Equipo</h3>
		<div class="row">
			<form v-on:submit.prevent="saveEquipo" class="col s12">
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="equipo_id" required>
						<label>Equipo ID</label>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="equipo" required>
						<label>Equipo</label>
					</div>
				</div>
				<div class="row">
					<div class="input-check col s12">
						<input type="checkbox" class="filled-in" v-model="activo">
      					<label for="filled-in-box">Activo</label>
					</div>
				</div>
				<button type="submit" class="btn">Ingresar</button>
				<router-link to="/" class="btn grey">Atras</router-link>
			</form>
		</div>
	</div>
</template>
<script>
import db from './firebaseInit'
export default {
	name: 'new-equipo',
	data () {
		return {
			equipo_id: null,
			equipo: null,
			activo: null
		}
	},
	methods: {
		saveEquipo () {
			db.collection('equipos').add({
				equipo_id: this.equipo_id,
				equipo: this.equipo,
				activo: this.activo
			})
			.then(docRef => {
				this.$router.push('/')
			})
			.catch(error => console.log(err))
		}
	}
}
</script>


