<template>
	<div id="edit-equipo">
		<h3>Editar Equipo</h3>
		<div class="row">
			<form v-on:submit.prevent="updateEquipo" class="col s12">
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="equipo_id" disabled="true">
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="equipo" required>
					</div>
				</div>
				<div class="row">
					<div class="input-check col s12">
						<input type="checkbox" class="filled-in" id="checkbox" v-model="activo">
						<label for="checkbox">{{ checked }}</label>
					</div>
				</div>
				<div class="row">
					<FileUpload/>
				</div>
				<button type="submit" class="btn">Actualizar</button>
				<router-link to="/" class="btn grey">Atras</router-link>
			</form>
		</div>
	</div>
</template>
<style scoped>
    img{
        max-height: 36px;
    }
</style>
<script>
import db from './firebaseInit'
export default {
	name: 'edit-equipo',
	data () {
		return {
			equipo_id: null,
			equipo: null,
			activo: null
		}
	},
	beforeRouteEnter (to, from, next) {
		db.collection('equipos').where('equipo_id', '==', to.params.equipo_id).get().then(querySnapshot => {
			querySnapshot.forEach(doc => {
				next(vm => {
					vm.equipo_id = doc.data().equipo_id
					vm.equipo = doc.data().equipo
					vm.activo = doc.data().activo
				})
			})
		})
	},
	watch: {
		'$route': 'fetchData'
	},
	methods: {
		fetchData () {
			db.collection('equipos').where('equipo_id', '==', this.$route.params.equipo_id).get().then(querySnapshot => {
			querySnapshot.forEach(doc => {
					this.equipo_id = doc.data().equipo_id
					this.equipo = doc.data().equipo
					this.activo = doc.data().activo
				})
			})
		},
		updateEquipo () {
			db.collection('equipos').where('equipo_id', '==', this.$route.params.equipo_id).get().then(querySnapshot => {
			querySnapshot.forEach(doc => {
					doc.ref.update({
						equipo_id: this.equipo_id,
						equipo: this.equipo,
						activo: this.activo
					})
					.then(() => {
					this.$router.push({name: 'view-equipo', params: {equipo_id: this.equipo_id}})
					})
				})
			})
		}
	}
}
</script>


