<template>
	<div id="dashboard">
		<ul class="collection with-header">
			<li class="collection-header"><h4>Equipos</h4></li>
			<li v-for="equipo in equipos" :key="equipo.equipo_id" class="collection-item">
				<router-link :to="{name: 'view-equipo', params: {equipo_id: equipo.equipo_id}}" class="chip">{{equipo.equipo_id}}</router-link>
				<router-link :to="{name: 'view-equipo', params: {equipo_id: equipo.equipo_id}}">{{equipo.equipo}}</router-link>

			<router-link class="secondary-content" :to="{name: 'view-equipo', params: {equipo_id: equipo.equipo_id}}">
				<i class="fa fa-eye"></i>
			</router-link>
			</li>
		</ul>
		<div class="fixed-action-btn">
			<router-link to="/new" class="btn-floating btn-large red">
				<i class="fa fa-plus"></i>
			</router-link>
		</div>
	</div>
</template>
<script>
import db from './firebaseInit'
export default {
	name: 'dashboard',
	data () {
		return {
			equipos: []
		}
	},
	created () {
		db.collection('equipos').get().then(querySnapshot => {
			querySnapshot.forEach(doc => {
				const data = {
					'id': doc.id,
					'equipo_id': doc.data().equipo_id,
					'equipo': doc.data().equipo,
					'activo': doc.data().activo
				}
				this.equipos.push(data)
			})
		})
	}
}
</script>


